.togglecheckbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.toggleslot {
  width: 45px;
  height: 25px;
  top: 50px;
  border-radius: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 2px 9px;
  position: absolute;
  left: 50%;
  top: 20px;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  cursor: pointer;
}

.togglebutton {
  -webkit-transform: translate(-0.4em, 0em);
          transform: translate(-0.4em, 0em);
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  transition: background-color 250ms, border-color 250ms, -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: background-color 250ms, border-color 250ms, transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: background-color 250ms, border-color 250ms, transform 500ms cubic-bezier(.26,2,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
}

.togglecheckbox:checked ~ .toggleslot .togglebutton {
  -webkit-transform: translate(1.7em, 0em);
          transform: translate(1.7em, 0em);
}

.gg-sun {
  position: absolute;
  right: 10px;
  bottom: 3px;
  color: #ffbb52;
}

.suniconwrapper {
  position: absolute;
  height: 30px;
  width: 30px;
  opacity: 0;
  -webkit-transform: translate(0em, 0em) rotate(0deg);
          transform: translate(0em, 0em) rotate(0deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: opacity 150ms, -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
}

.togglecheckbox:checked ~ .toggleslot .suniconwrapper {
  opacity: 1;
  -webkit-transform: translate(0em, 0em) rotate(0deg);
          transform: translate(0em, 0em) rotate(0deg);
}

.gg-moon {
  position: absolute;
  color: #ffbb52;
  left: 30px;
  top: 4px;
}

.mooniconwrapper {
  position: absolute;
  height: 30px;
  width: 30px;
  opacity: 1;
  -webkit-transform: translate(0em, 0em) rotate(0deg);
          transform: translate(0em, 0em) rotate(0deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: opacity 150ms, -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71);
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71), -webkit-transform 500ms cubic-bezier(.26,2,.46,.71);
}

.togglecheckbox:checked ~ .toggleslot .mooniconwrapper {
  opacity: 0;
  -webkit-transform: translate(0.3em, 0.1em);
          transform: translate(0.3em, 0.1em);
}

